@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,700;1,400;1,500;1,700&display=swap);
html {
  scroll-behavior: smooth;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
   font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #fff;
  background-color: rgb(15, 22, 36);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

a { 
  text-decoration: none;
  cursor: pointer;
}

a:link {
  color: inherit;
}

/* visited link */
a:visited {
  color: inherit;
}

.logo {
    margin: auto;
}

.logo__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    /* margin-right: 9em; */
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: wrap;
    margin-top: 0.5em;
}

.social__container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin-left: 9em;
}

.social__icons {
    padding: 0.5rem;
}

.social__icons:hover {
    background-color: rgba(255,255,255,0.2);
    border-radius: 50%;
    cursor: pointer;
}

.navbar__links { 
    order: 3;
    color: #979DAC;
    font-weight: 500;
}

.navbar__links a { 
    padding: 0 1em;
}

@media only screen and (min-width: 600px) {
    .navbar__links { 
        order: 2;
    }

    .social__container {
        order: 3;
        margin: 0;
    }

    .navbar {
        flex-wrap: nowrap;
    }
}

@media only screen and (min-width: 768px) {
    .navbar {
        padding: 1em;
        font-size: 1.2em;
    }

    .logo__container {
        margin-right: 1em;
    }

    .css-i4bv87-MuiSvgIcon-root {
        font-size: 2rem !important;
        padding: 1rem;
    }

    #cloud {
        padding: 0 !important;
    }

}

@media only screen and (min-width: 1024px) {
    .logo__container {
        width: 192px;
        margin-right: 0;
    }

    .logo {
        margin: 0;
    }

    .navbar {
        width: 875px;
        justify-content: space-between;
        margin: 0.5em auto;
        padding: 0;
    }
}



@media only screen and (min-width: 1400px) {
    .navbar {
        width: 1200px;
        justify-content: space-between;
    }
}
.title {
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-image: linear-gradient(121.57deg,#fff 18.77%,hsla(0,0%,100%,.66) 60.15%);
    background-size: 100%;
    margin: 0;
    text-align: left;
    margin-bottom: 0.5em;
}

.about { 
    margin-left: 2em;
}

.about__description { 
    color: #7D8597;
    line-height: 1.6;
    margin-bottom: 1.5em;
    font-weight: 500;
}

.btn__container {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 100%;
    margin: 0 auto;
    cursor: pointer;
}

.btn {
    flex: 1 1 auto;
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: white;
    text-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    border-radius: 35px;
    width: 100%;
    margin: auto;
    font-weight: bold;
}

.btn:hover {
    background-position: right center; /* change the direction of the change here */
}

.gradient__btn { 
    background-image: linear-gradient(to right, #fda8e5 0%, #92b3ec 51%, #fda8e5 100%);
    /* letter-spacing: 2px; */
}

.BgAnimation {
    background-color: transparent;
    overflow: hidden;
    position: absolute;
    right: 0;
    top: 60px;
    width: 200px;
    z-index: -1;
}

.BgAnimation__svg {
    height: 300px;
    width: 300px;
    -webkit-clip-path: polygon(25% 3%,75% 3%,97% 28%,97% 75%,75% 97%,25% 97%,3% 75%,3% 25%);
            clip-path: polygon(25% 3%,75% 3%,97% 28%,97% 75%,75% 97%,25% 97%,3% 75%,3% 25%);
}

@media only screen and (min-width: 600px) {
    .BgAnimation__svg {
        height: 450px;
        width: 450px;
        margin-left: 28em;
    }

    .BgAnimation {
        width: 100vw;
        display: flex;
        justify-content: center;
    }

    .btn__container {
        width: 255px;
        margin-left: 0;
    }
}

@media only screen and (min-width: 1024px) {
    h1.title {
        width: 450px;
    }
    
    h4.about__description {
        width: 600px;
    }

    .about {
        margin: 0 -80px;
        padding: 40px 80px 0;
    }

}
.experience-body {
    margin-top: 2em;
    margin-left: auto;
    margin-right: auto;
    text-align: left;
}

.job-btn {
    background: transparent;
    border-color: transparent;
    transition: all 0.3s linear;
    cursor: pointer;
    line-height: 1;
    outline-color: hsl(0, 0%, 0%);
    font-family: inherit;
    font-size: clamp(1em, 5vh, 1.25em);
    display: inline-block;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    margin-bottom: 0.5em;
}

.active-btn {
    color: #fff;
    border-bottom: 2px solid #fff;
}

.position {
    font-weight: normal;
    font-size: 1.25em;
}

.job {
    width: 100%;
    margin-top: 0;
    margin-bottom: 1em;
}

.job-desc {
    display: flex;
}

.right {
    margin-right: 1em;
    font-size: 1em;
}

.company {
    color: #FFF;
    background-color: #BBBFCA;
    border-radius: 0.25rem;
    padding: .375rem .75rem;
    width: auto;
    display: inline-block
}

.date {
    color: hsl(0, 0%, 30%);
}

.MuiTabs-indicator {
    background: linear-gradient(to right, #fda8e5 0%, #92b3ec 51%, #fda8e5 100%);
}

.MuiTab-root {
    color: #7D8597 !important;
}

.job__big {
    display: none;
}

.Mui-selected {
    color: #fda8e5 !important;
    font-weight: 600 !important;
}

@media only screen and (min-width: 1024px) {
    .job__big {
        display: flex;
        justify-content: space-between;
    }

    .job__container {
        align-self: flex-start;
        width: 100%;
        margin-left: 4em;
    }

    .job__small {
        display: none;
    }
    
    .MuiTab-root {
        padding: 0 !important;
    }
}

.company__link {
    color: #fda8e5 !important;
    /* background: linear-gradient(to right, #92b3ec 0%, #fda8e5 51%, #fda8e5 100%); */
    font-weight: 600 !important;
}

.projects__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap; 
}

.project__container { 
    display: flex;
    flex-direction: column;
    width: 310px;
    margin: 2em auto;
    border: 1px solid rgba(2,12,27,0.7);;
    box-shadow: 2px 2px 2px 2px rgba(2,12,27,0.7);;
    justify-content: flex-start;
}

.project__content {
    padding: 1em;
    font-size: 15px;
    color: #979DAC;
}

.project__content h3 {
    color: #fff;
}

.project__technologies {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-content: center;
}

.project__technologies span {
    margin-right: 2em;
}

.icon__container {
    margin-top: 1em;
    color: #fff;
}

.project__img img {
    width: 310px;
}

.project__container h4 { 
    margin-bottom: 0.5em;
}

.project__container p {
    margin-top: 0;
}

@media screen and (min-width: 430px) and (max-width: 1024px) {
    .project__container {
        width: 400px;
    }

    .project__img img {
        width: 400px;
    }
}

@media screen and (min-width: 1024px) {
    .project__container { 
        width: 500px;
    }
    .project__img img {
        width: 500px;
    }
}
    


.footer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.container {
    margin: 1em 1em;
}

.gradient__hr {
    background-image: linear-gradient(to right,#92b3ec 0%, #fda8e5 51%, #fda8e5 100%);
    border: none;
    height: 2px;
    width: 45px;
    margin-left: 0;
    margin-top: 2.5em;
}

.MuiFormControl-root {
    width: 100% !important;
}

.MuiInputLabel-root, .MuiOutlinedInput-input {
    font-family: "Montserrat",sans-serif !important;
    color: #5C677D !important;
}

.MuiOutlinedInput-root, .-MuiOutlinedInput-root {
    width: 100% !important;
    background-color: #161C27;
    color: #5C677D !important;
    margin-bottom: 1em;
}

.MuiInputLabel-root {
    color: #5C677D !important;
}

.form__container {
    width: 100%;
}

.email__container {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 100%;
}

.email {
    flex: 1 1 auto;
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    transition: 0.5s;
    background-size: 200% auto;
    color: #fff !important;
    text-shadow: 0px 0px 10px rgba(0,0,0,0.2);
    border-radius: 10px;
    width: 100%;
    margin: auto;
    font-weight: bold;
    margin-top: 0.5em;
    margin-bottom: 1.5em;
}

.email:hover {
    background-position: right center; /* change the direction of the change here */
}

.gradient__btn { 
    background-image: linear-gradient(to right, #fda8e5 0%, #92b3ec 51%, #fda8e5 100%);
    /* letter-spacing: 2px; */
}

.footer__social {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}

@media only screen and (min-width: 768px) {
    .container {
        width: 650px;
        margin: auto;
    }

    .gradient__hr {
        border-radius: 10px;
        height: 3px;
        margin-bottom: 1em;
    }

    .form__container, .email__container, .MuiOutlinedInput-root, .MuiOutlinedInput-root {
        width: 650px !important;
    }

}

@media only screen and (min-width: 1024px) {
    .container {
        width: 850px;
    }
}

@media only screen and (min-width: 1400px) {
    .container {
        width: 1200px;
    }
}

